@import url(https://fonts.googleapis.com/css2?family=Yantramanav:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Asap:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  background-color: rgb(47,48,78);
}


.divtitle{ 
    padding: 20px 20px;
    background-color: whitesmoke;
}

.title{
    font-weight: lighter;
    background-color: whitesmoke;
    text-align: center;
    font-family: Asap;
    color: black;
    font-size: 25px;
    padding-top: 5px 5px;
}


.divcounter{
    text-align: center;
}

.counter{
    font-weight: lighter;
    position: relative;
    top: 20vh;
    text-align: center;
    font-family: asap;
    font-size: 50px;
    color: whitesmoke;
}





.btns{
    text-align: center;
    position: relative;
    top: 25vh;
}



.number{
    text-align: center;
    font-size: 50px;
    margin: 50px auto;
    color: whitesmoke;
    border: 3px solid whitesmoke;
    max-width: 150px;
    font-family: asap, sans-serif;
    border-radius: 10px;
}


#btnminus{
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
    outline: none;
    text-align: center;
    color: white;
    font-size: 150%;
    display: inline-block;
    font-family: asap;
    border: 3px solid whitesmoke;
    margin: 15px 15px;
    max-width: 25%;
    min-width: 10%;
    padding: 5px 5px;
    border-radius: 5px;
    transition: background-color 0.5s ease;
    background-color: #2f304e;
}

#btnplus{
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
    outline: none;
    text-align: center;
    color: white;
    font-size: 150%;
    display: inline-block;
    font-family: asap;
    border: 3px solid whitesmoke;
