}

#btnreset{
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
    outline: none;
    text-align: center;
    color: white;
    font-size: 150%;
    display: inline-block;
    font-family: asap;
    border: 3px solid whitesmoke;
    margin: 15px 15px;
    max-width: 25%;
    min-width: 10%;
    padding: 5px 5px;
    border-radius: 5px;
    transition: background-color 0.5s ease;
    background-color: #2f304e;
}






.divcbc{
    text-align: center;
    font-family: Asap;
}

#cbc{
    position: relative;
    font-family: Asap;
    font-size: 30px;
    color: white;
    top: 40vh;
}
